<template>
  <div id="app">
    <div class="back" v-show='isShow'>
      <!-- <dianchangxiugai></dianchangxiugai> -->
      <div class="card1">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="电厂名称">
              <el-input v-model="form.powerPlantName"></el-input>
            </el-form-item>
            <el-form-item label="电厂类型">
              <el-select v-model="powerPlantType" placeholder="请选择">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item lable-width="120px" label="发电机组容量" width='120'>
              <el-select v-model="generatorCapacity" placeholder="请选择">
                <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="升压站电压等级">
              <el-select v-model="voltageLevel" placeholder="请选择">
                <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="所在省份">
              <el-select v-model="province" placeholder="请选择">
                <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="电厂地址">
              <el-input v-model="form.address"></el-input>
            </el-form-item>
            <el-form-item label="电话">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
            <el-form-item label="邮编">
              <el-input v-model="form.postcode"></el-input>
            </el-form-item>
            <el-button type="primary" @click='Show' >修改 </el-button>
          </el-form>
        </div>
    </div>
    <div class="card2">
       <!-- <h2>{{ $route.query }}</h2> -->
      <div class="card2_top">
        <el-form :inline="true" :model="form" class="demo-form-inline">
          <el-form-item label="电厂名称" >
            <el-input v-model="form.powerPlantName" placeholder="电厂名称" id="btn0"></el-input>
          </el-form-item>
          <el-form-item label="电厂类型">
            <el-select v-model="powerPlantType" placeholder="电厂类型" id="btn1">
              <el-option label="水电厂" value='t1'></el-option>
              <el-option label="火电厂" value='t2'></el-option>
              <el-option label="风电厂" value='t3'></el-option>
              <el-option label="雷电厂" value='t4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="机组容量">
            <el-select v-model="generatorCapacity" placeholder="活动区域"  id="btn2"> 
              <el-option label="10L" value='s1'></el-option>
              <el-option label="20L" value='s2'></el-option>
              <el-option label="30L" value='s3'></el-option>
              <el-option label="40L" value='s4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="电压等级">
            <el-select v-model="voltageLevel" placeholder="活动区域"  id="btn3">
              <el-option label="1" value='l1'></el-option>
              <el-option label="2" value='l2'></el-option>
              <el-option label="3" value='l3'></el-option>
              <el-option label="3" value='l4'></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click='search()'>查询</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="info" @click='reback'>重置</el-button>
          </el-form-item>
          <el-form-item>
            <el-button type="success" @click='add'>+新增</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div id="card2_bottom">
        <el-table :data="list.slice((currpage - 1) * pagesize, currpage * pagesize)" style="width: 100%"
          @selection-change="handleSelectionChange" >
          <el-table-column prop="powerPlantName" label="电厂名称">
          </el-table-column>
          <el-table-column prop="powerPlantType" label="电厂类型">
          </el-table-column>
          <el-table-column prop="generatorCapacity" label="发电组容量">
          </el-table-column>
          <el-table-column prop="voltageLevel" label="升压站电压等级">
          </el-table-column>
          <el-table-column prop="address" label="地址">
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small" :data='list.id' >删除
              </el-button>
              <el-button @click="handleClick2(scope.row),Show()" type="text" size="small" >修改
              </el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next, sizes, total, jumper" :page-sizes="[5, 10, 15, 20]"
          :page-size="pagesize" :total="list.length" @current-change="handleCurrentChange"
          @size-change="handleSizeChange">
        </el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import Qs from 'qs'//引入qs
  import dianchangxiugai from './dianchangxiugai.vue'
  export default {
    name: 'xinjian',
    data() {
      return {
        isShow:false,
        
          form: {
            name: '',
            region: '',
  
          },
          formInline: {
            user: '',
            region: ''
          },
          options: [{
          value: '水电厂'
        }, {
          value: '火电厂'
        }, {
          value: '风电厂'
        }, {
          value: '气电厂'
        }],
        options1: [{
          value: '10L'
        }, {
          value: '20L'
        }, {
          value: '30L'
        }, {
          value: '40L'
        }],
        options2: [{
          value: '特级'
        }, {

          value: '1'
        }, {

          value: '2'
        }, {

          value: '3'
        }],
        options3: [{

          value: '陕西省'
        }, {

          value: '山西省'
        }, {

          value: '山东省'
        }, {

          value: '湖北省'
        }],
        powerPlantType: '',
        generatorCapacity: '',
        voltageLevel: '',
        province: '',
        form: {
          powerPlantName: '',
          address: '',
          phone: '',
          postcode: '',
        },
        msg: 8888,
        list3:[

        ],
        list2:[

        ],
        list:[
        ],
        pagesize: 5,
        currpage: 1
      }
    },
    components:{
      dianchangxiugai
    },
    computed:{
     
    },

    created(){
      axios.get('http://127.0.0.1:8084/power/findall')
      //http://106.54.54.237:8000/api/v1/home/data?type=sell&page=1
          //then获取成功；response成功后的返回值（对象）

          .then(response => {

            console.log(response)
             
           
             this.list = response.data.data.filter(item => {
              return item.infoState == 0;
            })

            this.list2 = this.list;
            // console.log('list:' +this.list);

            // console.log('list2:'+ this.list2);
          })

          //获取失败

          .catch(error => {

            console.log(error);

            alert('网络错误，不能访问');

          })

    },

    methods: {
      
      add(){
           this.$router.push({
            path: './dianchang',

          })
      },
      handleClick(a){
       
        axios.get(`http://127.0.0.1:8084/power/delete?powerPlantID=${a.powerPlantID}`).then( result => {
            // 只有状态码为200,才能返回true,说明注册成功,其他状态码全部失败！
           
        
           
        } ).catch( ( error ) => {
          
            console.log(error);
            if (error.response) {
                // 请求已发出，但服务器响应的状态码不在 2xx 范围内
                console.log( error.response.data );
                console.log( error.response.status );
                console.log( error.response.headers );
            } else {
                // Something happened in setting up the request that triggered an Error
                console.log( "Error", error.message );
            }
            console.log( error.config );
        } );
        alert('确认删除？')
        window.location.reload();
      },
      handleClick2(a){
         var obj = {};
         alert(a.powerPlantName)
        
         obj = a;
         console.log(obj);
         console.log(obj.powerPlantID);
         console.log(obj.powerPlantName);
         console.log(obj.address);
         console.log(obj.powerPlantType);
         console.log(obj.generatorCapacity);
         console.log(obj.voltageLevel);
         console.log(obj.province);
      },
      reback(){
        document.getElementById('btn0').value='';
        // console.log(this.list2);
        this.list = this.list2;
      },
      Show(){
        this.isShow = !this.isShow;
      },
      handleCurrentChange(cpage) {
        this.currpage = cpage;
      },
      handleSizeChange(psize) {
        this.pagesize = psize;
      },
      handleSelectionChange(val) {
        console.log(val)
      },
      search() {
       var obtn1 = document.getElementById('btn0').value
       var obtn2 = document.getElementById('btn1').value
       var obtn3 = document.getElementById('btn2').value
       var obtn4 = document.getElementById('btn3').value

      //  console.log(obtn1, obtn2, obtn3, obtn4)
       axios.get(`http://127.0.0.1:8084/power/findcondition?powerPlantName=${obtn1}&powerPlantType=${obtn2}&generatorCapacity=${obtn3}&voltageLevel=${obtn4}`).then( result => {
            // 只有状态码为200,才能返回true,说明注册成功,其他状态码全部失败！
            alert('ok')
            // return result.data.code === 200;
            // this.list = result.data.data;
            console.log(obtn1, obtn2, obtn3, obtn4)
            this.list3 = this.list;
           this.list = result.data.data;
            // console.log(this.list)
            // this.list[3] = result.data.data;
        } ).catch( ( error ) => {
            // 【务必注意】这里的error输出的不是一个对象【error.response才是一个对象】
            console.log(error);
            if (error.response) {
                // 请求已发出，但服务器响应的状态码不在 2xx 范围内
                console.log( error.response.data );
                console.log( error.response.status );
                console.log( error.response.headers );
            } else {
                // Something happened in setting up the request that triggered an Error
                console.log( "Error", error.message );
            }
            console.log( error.config );
        } );

      },
    mounted() {
      

    
     
    }
  }
  }
  // alert(this.isShow)
</script>

<style lang="" scoped>
  /* #app{
    width: 100%;
    height: 100%;
    position: relative;
  } */
  .back{
    position: absolute;
    width: 100%;
    height: 100%;
    background: black;
    background-color:rgba(0,0,0,0.5);
    z-index: 2;
  }
  .el-input {
    width: 150px;
    height: 25px;
  }

  .el-select {
    width: 150px;
    height: 25px;
  }

  .el-form-item {
    margin-bottom: 10px;
  }

  .el-button {
    padding: 4px 8px;
  }


  .card2 {
    text-align: center;
    width: 95%;
    margin: auto;
    background: white;
  }

  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
  .el-form-item__label{
    width: 100px !important;
    padding: 0 !important;
  }
  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style{
    width: 120px;
  }

  
</style>



